import type {ReactNode, VFC} from 'react';
import React, {useEffect, useState} from 'react';
import {MdContentCopy} from 'react-icons/md';
import classNames from 'classnames';
import useCountdown from '../hooks/useCountdown';
import Tooltip from './Tooltip';
import Corset from './Corset';
import Animate from './Animate';

type Props = {
  totalSupply: number;
  circulatingSupply: number;
  burnedSupply: number;
  price: number;
  symbol: string;
  smartContactAddress: string;
  decimals: number;
};

const UpperDetail: VFC<{
  name: ReactNode;
  value: ReactNode;
  button?: ReactNode;
}> = ({name, value, button}) => (
  <div className="flex flex-col-reverse text-xl">
    <div className="text-gray-400">{name}</div>
    <Animate>
      <div className="flex items-center">
        <div
          className={classNames('uppercase overflow-hidden truncate', {
            'mr-2': button != null,
          })}
        >
          {value}
        </div>
        {button}
      </div>
    </Animate>
  </div>
);

const LowerDetail: VFC<{
  index: number;
  name: ReactNode;
  value: number;
  format?: (value: number) => string;
}> = ({index, name, value, format = n => n.toLocaleString()}) => {
  const currentValue = useCountdown({to: value});
  return (
    <Animate delay={index * 200}>
      <div className="rounded-xl bg-gray-900 bg-opacity-75 text-center p-2 text-xl shadow-fire">
        <div className="text-gray-400">{name}</div>
        <div className="truncate">{format(currentValue)}</div>
      </div>
    </Animate>
  );
};

const TokenInfo: VFC<Props> = ({
  totalSupply,
  circulatingSupply,
  burnedSupply,
  price,
  symbol,
  smartContactAddress,
  decimals,
}) => {
  const [showTooltip, setShowTooltip] = useState(false);

  useEffect(() => {
    if (showTooltip) {
      const timeoutId = setTimeout(() => {
        setShowTooltip(false);
      }, 3000);
      return () => {
        clearTimeout(timeoutId);
      };
    }
  }, [showTooltip]);

  return (
    <Corset>
      <div className="rounded-xl flex flex-col lg:flex-row justify-around px-4 py-8 bg-gray-900 bg-opacity-75 space-y-2 lg:space-y-0 mb-4 isolate shadow-fire">
        <h2 className="text-3xl">Token Info</h2>
        <UpperDetail name="Symbol" value={symbol} />
        <UpperDetail
          name="Smart Contract Address"
          value={smartContactAddress}
          button={
            <>
              <button
                type="button"
                onClick={() => {
                  void window.navigator.clipboard.writeText(
                    smartContactAddress,
                  );
                  setShowTooltip(true);
                }}
              >
                <MdContentCopy />
              </button>
              {showTooltip && <Tooltip>Copied!</Tooltip>}
            </>
          }
        />
        <UpperDetail name="Decimals" value={decimals.toLocaleString()} />
      </div>
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
        <LowerDetail index={0} name="Total Supply" value={totalSupply} />
        <LowerDetail
          index={1}
          name="Circulating Supply"
          value={circulatingSupply}
        />
        <LowerDetail index={2} name="Burned Supply" value={burnedSupply} />
        <LowerDetail
          index={3}
          name="Price"
          value={price}
          format={n => `$${n.toFixed(10)}`}
        />
      </div>
    </Corset>
  );
};

export default TokenInfo;
