import type {ReactNode, FC} from 'react';
import React, {useCallback, useState} from 'react';
import {useMedia} from 'react-use';
import {BREAKPOINTS} from '../constants';
import Menu from './Menu';
import Overlay from './Overlay';
import CollapsibleIcon from './CollapsibleIcon';
import Collapsible from './Collapsible';

/** renders a button that, when clicked, toggles a menu */
const DropdownMenu: FC<{label: ReactNode}> = ({label, children}) => {
  const [isOpen, setIsOpen] = useState(false);
  const isSmOrBigger = useMedia(`(min-width: ${BREAKPOINTS.sm}px)`);

  const close = useCallback(() => {
    setIsOpen(false);
  }, []);

  return (
    <div className="mx-4">
      {isOpen && isSmOrBigger && <Overlay close={close} />}
      <button
        type="button"
        className="flex w-full sm:w-auto justify-center items-center font-bold"
        onClick={() => {
          setIsOpen(is => !is);
        }}
      >
        {label} <CollapsibleIcon isOpen={isOpen} />
      </button>
      {isOpen && isSmOrBigger && <Menu>{children}</Menu>}
      {!isSmOrBigger && <Collapsible isOpen={isOpen}>{children}</Collapsible>}
    </div>
  );
};

export default DropdownMenu;
