import classNames from 'classnames';
import type {ComponentProps} from 'react';
import {forwardRef} from 'react';
import {Link} from 'react-router-dom';
import {getButtonClasses} from './Button';

type Props = ComponentProps<typeof Link> & {theme?: 'dark' | 'light'};

const ButtonRouterLink = forwardRef<HTMLAnchorElement, Props>(
  ({children, theme, className, ...rest}, ref) => (
    <Link
      ref={ref}
      {...rest}
      className={classNames(getButtonClasses({theme}), className)}
    >
      {children}
    </Link>
  ),
);

ButtonRouterLink.displayName = 'ButtonRouterLink';

export {getButtonClasses};
export default ButtonRouterLink;
