import classNames from 'classnames';
import type {FC, VFC} from 'react';
import type {SocialMediaAccount} from '../types';

const FloatingSocialIcon: FC<JSX.IntrinsicElements['a']> = ({
  href,
  children,
}) => (
  <a
    href={href}
    target="_blank"
    rel="noopener noreferrer"
    className={classNames(
      'flex-auto w-12 h-12 min-h-0 aspect-w-1 aspect-h-1',
      'p-1 sm:p-2',
      'transform hover:scale-150 hover:-translate-x-1.5 duration-75',
    )}
  >
    {children}
  </a>
);

const FloatingSocialIcons: VFC<{
  accounts: Array<SocialMediaAccount>;
}> = ({accounts}) => {
  return (
    <div
      className={classNames(
        'flex justify-around flex-wrap sm:flex-nowrap sm:flex-col gap-8 sm:gap-4 max-h-screen',
        'sm:fixed sm:top-1/2 sm:right-0 sm:transform sm:-translate-y-1/2 z-50',
        'text-2xl sm:text-2xl md:text-4xl xl:text-5xl',
        'px-4 sm:px-2 py-16 sm:py-2',
        'text-black bg-black',
      )}
    >
      {accounts.map(({name, img, url}) => (
        <FloatingSocialIcon key={name} href={url}>
          <img
            src={img}
            alt={name}
            className="overflow-hidden w-12 object-contain"
          />
        </FloatingSocialIcon>
      ))}
    </div>
  );
};

export default FloatingSocialIcons;
