import type {VFC} from 'react';
import {useEffect, useState} from 'react';
import {
  getTotalSupply,
  getBalance,
  getPrice,
  BURN_ADDRESS,
  SYMBOL,
  DECIMALS,
  CONTRACT_ADDRESS,
} from '../contracts/UselessContract';
import TokenInfo from '../components/TokenInfo';

const UselessTokenInfo: VFC = () => {
  const [totalSupply, setTotalSupply] = useState(0);
  const [circulatingSupply, setCirculationSupply] = useState(0);
  const [burnedSupply, setBurnedSupply] = useState(0);
  const [price, setPrice] = useState(0);

  useEffect(() => {
    void (async () => {
      setTotalSupply(await getTotalSupply());
      setBurnedSupply(await getBalance(BURN_ADDRESS));
      setCirculationSupply(totalSupply - burnedSupply);
    })();
  }, [totalSupply, circulatingSupply, burnedSupply, price]);

  useEffect(() => {
    void (async () => {
      setPrice(await getPrice());
    })();
  }, [price]);

  return (
    <>
      <TokenInfo
        totalSupply={totalSupply}
        circulatingSupply={circulatingSupply}
        burnedSupply={burnedSupply}
        price={price}
        symbol={SYMBOL}
        smartContactAddress={CONTRACT_ADDRESS}
        decimals={DECIMALS}
      />
    </>
  );
};

export default UselessTokenInfo;
