import type {VFC} from 'react';
import {useEffect, useState} from 'react';
import Typed from 'react-typed';
import {MdHome} from 'react-icons/md';
import appWithPhone from '../assets/useless/appwithphone.png';
import Animate from '../components/Animate';
import ButtonLink from '../components/ButtonLink';
import ButtonRouterLink from '../components/ButtonRouterLink';
import intoTheSun from '../assets/images/into-the-sun.png';

const preload = async (src: string) => {
  const img = new Image();
  const promise = new Promise(resolve => {
    img.addEventListener('load', resolve);
  });
  img.src = src;
  await promise;
};

const SplashPage: VFC = () => {
  const [isDoneTyping, setIsDoneTyping] = useState(false);
  const text = 'How Useless at Crypto are you?';

  useEffect(() => {
    void preload(intoTheSun);
  }, []);

  return (
    <main>
      <div className="flex flex-col lg:flex-row min-h-screen w-full justify-between items-stretch p-4 lg:p-12">
        <div className="flex-1 flex flex-col justify-between gap-4 items-stretch p-8">
          <h1 className="text-4xl lg:text-6xl leading-tight">
            {isDoneTyping ? (
              text
            ) : (
              <Typed
                strings={[text]}
                typeSpeed={25}
                onComplete={() => setIsDoneTyping(true)}
              />
            )}
          </h1>
          {isDoneTyping && (
            <>
              <div className="flex flex-col lg:flex-row justify-around lg:text-2xl gap-4 overflow-hidden">
                <Animate from={{x: -500}}>
                  <ButtonLink
                    theme="light"
                    href="https://app.uselesscrypto.com/swap/"
                  >
                    Too Useless to read. I just ape
                  </ButtonLink>
                </Animate>
                <Animate from={{x: 500}} delay={1000}>
                  <ButtonLink
                    theme="light"
                    href="https://uselesscrypto.com/assets/litepaper/Useless_LitepaperV2.0.pdf"
                  >
                    I'd like some Useless technical information
                  </ButtonLink>
                </Animate>
              </div>
              <div className="flex flex-col gap-4 lg:flex-row lg:items-end lg:text-2xl">
                <div className="hidden lg:flex-1" />
                <div className="text-center">
                  <Animate delay={2000}>
                    <ButtonRouterLink to="/home" theme="light">
                      <div>
                        <MdHome className="text-6xl inline-block" />
                        <br />
                        Useless Home
                      </div>
                    </ButtonRouterLink>
                  </Animate>
                </div>
                <div className="flex-1 text-right">
                  <div className="flex justify-center lg:justify-end items-center">
                    <Animate delay={3000}>
                      <ButtonRouterLink to="/mobile-app" theme="light">
                        <div>
                          oosh an App?!
                          <br />
                          Take me HERE!
                        </div>
                        <img
                          src={appWithPhone}
                          alt="phone"
                          className="lg:hidden h-16"
                        />
                      </ButtonRouterLink>
                    </Animate>
                  </div>
                </div>
              </div>
            </>
          )}
        </div>
        <img
          src={appWithPhone}
          alt="phone"
          className="max-h-[80vh] hidden lg:block ml-[-5%] mr-[-5%]"
          style={{clipPath: 'polygon(25% 0, 100% 0, 100% 100%, 25% 100%)'}}
        />
      </div>
    </main>
  );
};

export default SplashPage;
