import {forwardRef} from 'react';
import classNames from 'classnames';

const getButtonClasses = ({
  theme = 'dark',
}: {
  theme?: 'dark' | 'light';
}): string =>
  classNames(
    'inline-flex justify-around items-center text-center',
    'w-full sm:w-auto',
    'uppercase font-bold tracking-wider',
    'bg-opacity-50 hover:bg-opacity-100',
    'border-2 rounded',
    'py-2 px-4',
    'transform duration-75',
    'active:translate-y-1',
    'shadow-xl',
    {
      'border-white bg-black hover:bg-white text-white hover:text-black':
        theme === 'dark',
      'border-black bg-white hover:bg-black text-black hover:text-white':
        theme === 'light',
    },
  );

type Props = JSX.IntrinsicElements['button'] & {theme?: 'dark' | 'light'};

const Button = forwardRef<HTMLButtonElement, Props>(
  ({children, theme, className, ...rest}, ref) => (
    <button
      ref={ref}
      {...rest}
      // eslint-disable-next-line react/button-has-type
      type={rest.type ?? 'button'}
      className={classNames(getButtonClasses({theme}), className)}
    >
      {children}
    </button>
  ),
);

Button.displayName = 'Button';

export {getButtonClasses};
export default Button;
