import Web3 from 'web3';
import {abi} from './interfaces/UselessAbi';

const CONTRACT_ADDRESS = '0x2cd2664ce5639e46c6a3125257361e01d0213657';
const BURN_ADDRESS = '0x000000000000000000000000000000000000dead';
const SYMBOL = 'Useless';
const DECIMALS = 9;

const web3 = new Web3('https://bsc-dataseed.binance.org/');

const contract = new web3.eth.Contract(abi, CONTRACT_ADDRESS);

const getTotalSupply = async (): Promise<number> => {
  return Math.round(
    Number(
      web3.utils.fromWei(
        String(await contract.methods.totalSupply().call()),
        'nano',
      ),
    ),
  );
};

const getBalance = async (address: string): Promise<number> => {
  return Math.round(
    Number(
      web3.utils.fromWei(
        String(await contract.methods.balanceOf(address).call()),
        'nano',
      ),
    ),
  );
};

const getPrice = async (): Promise<number> => {
  try {
    const response = await fetch(
      'https://app.uselesscrypto.com/api/contract/price',
    );
    if (!response.ok) {
      throw new Error(response.statusText);
    }
    const result: {price: string} = await response.json();
    return Number(result.price);
  } catch {
    return 0;
  }
};

export {
  getTotalSupply,
  getBalance,
  getPrice,
  CONTRACT_ADDRESS,
  SYMBOL,
  DECIMALS,
  BURN_ADDRESS,
};
