import classNames from 'classnames';
import type {ReactNode, VFC} from 'react';
import {useLayoutEffect, useRef, useState} from 'react';
import Hamburger from 'hamburger-react';
import {Link} from 'react-router-dom';
import {gsap} from 'gsap';
import Corset from './Corset';
import Overlay from './Overlay';

const MobileHeader: VFC<{
  logo: string;
  logoAlt: string;
  menus: ReactNode;
  cta: ReactNode;
}> = ({logo, logoAlt, menus, cta}) => {
  const menuRef = useRef<HTMLDivElement | null>(null);
  const [isOpen, setIsOpen] = useState(false);

  useLayoutEffect(() => {
    const menu = menuRef.current;
    if (!menu) {
      return;
    }

    gsap.to(menu, {
      opacity: isOpen ? 1 : 0,
      // x: isOpen ? 0 : 100,
      y: isOpen ? 0 : -100,
      duration: 1 / 10,
    });
  }, [isOpen]);

  return (
    <>
      <div className="fixed z-20 top-0 w-full flex items-center h-[48px] pl-2 justify-between bg-black">
        <div>
          <Link to="/home">
            <img src={logo} alt={logoAlt} className="w-24" />
          </Link>
        </div>
      </div>
      <button
        type="button"
        className={classNames(
          'fixed top-0 right-0 h-[48px] w-[48px] flex items-center justify-center z-30 transform duration-100',
          {
            '-translate-x-10 translate-y-10': isOpen,
          },
        )}
        onClick={() => setIsOpen(is => !is)}
      >
        <Hamburger toggled={isOpen} size={25} label="Toggle Menu" />
      </button>
      <div className="h-[48px]" />
      {isOpen && <Overlay close={() => setIsOpen(false)} />}
      <nav
        ref={menuRef}
        className={classNames(
          'fixed z-20 top-10 right-10 left-10 border-2 border-primary rounded bg-black flex flex-col items-center p-10 space-y-8',
        )}
      >
        {menus}
        {cta}
      </nav>
    </>
  );
};

const DesktopHeader: VFC<{
  logo: string;
  logoAlt: string;
  menus: ReactNode;
  cta: ReactNode;
}> = ({logo, logoAlt, menus, cta}) => (
  <div className="bg-gray-900 bg-opacity-75 pb-4">
    <Corset>
      <nav className="flex items-center">
        <div>
          <Link to="/home">
            <img src={logo} alt={logoAlt} />
          </Link>
        </div>
        <div className="flex-1" />
        {menus}
        {cta}
      </nav>
    </Corset>
  </div>
);

const Header: VFC<{
  logo: string;
  logoAlt: string;
  menus: ReactNode;
  cta: ReactNode;
}> = ({logo, logoAlt, menus, cta}) => (
  <header>
    <div className="hidden sm:block">
      {' '}
      <DesktopHeader logo={logo} logoAlt={logoAlt} menus={menus} cta={cta} />
    </div>
    <div className="sm:hidden">
      <MobileHeader logo={logo} logoAlt={logoAlt} menus={menus} cta={cta} />
    </div>
  </header>
);

export default Header;
