import classNames from 'classnames';
import type {VFC, ReactElement} from 'react';
import {useLayoutEffect} from 'react';
import ReactDOM from 'react-dom';

const BackgroundVideo: VFC<{
  sources: Array<ReactElement<JSX.IntrinsicElements['source'], 'source'>>;
  poster: string;
  className?: string;
}> = ({poster, sources, className}) => {
  useLayoutEffect(() => {
    document.body.classList.add('bg-black');
    return () => {
      document.body.classList.remove('bg-black');
    };
  }, []);

  // for some reason, the media attribute for video sources is not supported,
  // so we have to use a JS solution.
  const matchedSource = sources.find(
    source =>
      !source.props.media || window.matchMedia(source.props.media).matches,
  );

  return ReactDOM.createPortal(
    <video
      className={classNames(
        'fixed inset-0 w-full h-screen object-cover',
        className,
      )}
      autoPlay
      loop
      muted
      poster={poster}
      preload="auto"
    >
      {matchedSource}
    </video>,
    document.querySelector('#bg')!,
  );
};

export default BackgroundVideo;
