import classNames from 'classnames';
import {forwardRef} from 'react';
import {getButtonClasses} from './Button';

type Props = JSX.IntrinsicElements['a'] & {theme?: 'dark' | 'light'};

const ButtonLink = forwardRef<HTMLAnchorElement, Props>(
  ({children, theme, className, ...rest}, ref) => (
    <a
      ref={ref}
      {...rest}
      className={classNames(getButtonClasses({theme}), className)}
    >
      {children}
    </a>
  ),
);

ButtonLink.displayName = 'ButtonLink';

export {getButtonClasses};
export default ButtonLink;
