import classNames from 'classnames';
import type {ReactNode} from 'react';
import {forwardRef} from 'react';

type Props = {
  icon: ReactNode;
  heading: ReactNode;
  body: ReactNode;
  className?: string;
};

const IconTile = forwardRef<HTMLDivElement, Props>(
  ({icon, heading, body, className}, ref) => (
    <div
      ref={ref}
      className={classNames('flex flex-col items-center space-y-4', className)}
    >
      <div className="text-6xl w-16 h-16">{icon}</div>
      <h6 className="text-xl font-bold text-center min-h-[4rem]">{heading}</h6>
      <p className="text-white text-center">{body}</p>
    </div>
  ),
);

IconTile.displayName = 'IconTile';

export default IconTile;
